.home-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.hero-section {
  text-align: center;
  padding: 4rem 0;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-radius: 12px;
  margin-bottom: 3rem;
}

.hero-section h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.hero-section p {
  font-size: 1.2rem;
  opacity: 0.9;
}

.cta-button {
  display: inline-block;
  background: #28a745;
  color: white;
  padding: 1rem 2rem;
  border-radius: 8px;
  text-decoration: none;
  font-size: 1.1rem;
  margin-top: 2rem;
  transition: transform 0.2s;
}

.cta-button:hover {
  transform: translateY(-2px);
}

.premium-badge {
  display: inline-block;
  background: rgba(255, 255, 255, 0.2);
  padding: 1rem 2rem;
  border-radius: 8px;
  border: 2px solid white;
  font-size: 1.1rem;
  margin-top: 2rem;
}

.features-section {
  margin-bottom: 3rem;
}

.features-section h2 {
  text-align: center;
  margin-bottom: 2rem;
  font-size: 2.5rem;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.feature-card {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.feature-card h3 {
  color: #333;
  margin-bottom: 1rem;
}

.quick-actions {
  text-align: center;
}

.action-button {
  display: inline-block;
  background: #007bff;
  color: white;
  padding: 1rem 2rem;
  border-radius: 8px;
  text-decoration: none;
  margin: 0 1rem;
  transition: background 0.2s;
}

.action-button:hover {
  background: #0056b3;
}

.action-button.secondary {
  background: #6c757d;
}

.action-button.secondary:hover {
  background: #545b62;
}

@media (max-width: 768px) {
  .hero-section h1 {
    font-size: 2rem;
  }
  
  .features-grid {
    grid-template-columns: 1fr;
  }
  
  .quick-actions {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .action-button {
    margin: 0;
  }
}
